import React from "react";
import Layout from "../components/layout";
import Header from "../components/common/header";
import Footer from "../components/common/footer";
import { Country } from "country-state-city";

const Blog = (props) => {
  const { pageContext } = props;
  console.log("pageContext::", pageContext);
  console.log("props::", props);

  const countries = Country.getAllCountries();
  const init = {
    name: "",
    email: "",
    country: "",
    contact: "",
    message: "",
  };
  const [talkToUsInp, setTalkToUsInp] = React.useState(init);

  function handleTalkToUsInpChange(event) {
    const { name, value } = event.target;
    console.log("valuesOnchange::", value);
    setTalkToUsInp((prevFormData) => ({ ...prevFormData, [name]: value }));
  }

  const handleSubmit = async (e) => {
    e.preventDefault();
    // const clients = collection(db, "clients");
    // console.log("clients::", clients);
    // await addDoc(clients, talkToUsInp);
    // utils.showSuccssMsg("We will contact/talk to you soon");
    setTalkToUsInp(init);
  };

  return (
    <Layout>
      <section className="mt-5">
        <div className="container">
          <div className="row ">
            <div className="col-md-12 col-lg-9">
              <div className="row">
                <div className="col-md-12 col-lg-7">
                  <div className="blog_wrapper">
                    <h2 className="fs-1 mb-3">{pageContext?.name}</h2>
                    <span className="fs-3">({pageContext?.language})</span>
                  </div>
                  <div
                    dangerouslySetInnerHTML={{
                      __html: pageContext?.description,
                    }}
                    className="blog-content mt-3"
                  ></div>
                </div>
                <div className="col-md-12 col-lg-5">
                  <div className="blog_img shadow">
                    <img src={pageContext?.image} alt="blog-img" />
                  </div>
                </div>
              </div>
            </div>
            <div className="col-md-12 col-lg-3">
              <div className="card sticky-top section-cta">
                <div className="card-header">Categories</div>
                <div className="card-body">
                  <form onSubmit={handleSubmit} className="cta-form">
                    <div>
                      <label for="full-name">Name</label>
                      <input
                        id="full-name"
                        type="text"
                        placeholder="Enter Your Name"
                        name="name"
                        value={talkToUsInp.name}
                        onChange={(e) => handleTalkToUsInpChange(e)}
                        required
                      />
                    </div>
                    <div>
                      <label for="email">Email address</label>
                      <input
                        id="email"
                        type="email"
                        placeholder="me@example.com"
                        name="email"
                        value={talkToUsInp.email}
                        onChange={(e) => handleTalkToUsInpChange(e)}
                        required
                      />
                    </div>
                    <div>
                      <label for="select-where">
                        Where did you hear from us?
                      </label>
                      <select
                        name="country"
                        onChange={(e) => handleTalkToUsInpChange(e)}
                        id="select-where"
                        required
                      >
                        <option value="">Please choose one option:</option>
                        {countries.map((ele, ind) => {
                          return (
                            <option key={ind} value={ele.isoCode}>
                              {ele.name}
                            </option>
                          );
                        })}
                      </select>
                    </div>
                    <div>
                      <label for="mobile">Skype / Whatsapp / Mobile</label>
                      <input
                        id="mobile"
                        type="tel"
                        placeholder="Skype / Whatsapp / Mobile"
                        name="mobile"
                        value={talkToUsInp.mobile}
                        onChange={(e) => handleTalkToUsInpChange(e)}
                        required
                      />
                    </div>
                    <div>
                      <label for="content">Content</label>
                      <textarea
                        id="content"
                        placeholder="Type content here"
                        name="content"
                        value={talkToUsInp.content}
                        onChange={(e) => handleTalkToUsInpChange(e)}
                        rows={5}
                        className="input"
                        required
                      ></textarea>
                    </div>
                    <button className="btn btn--form">Submit</button>
                  </form>
                </div>
              </div>
            </div>
          </div>
          <div></div>
        </div>
      </section>
    </Layout>
  );
};

export default Blog;
